.pagecompany{
  &-info{
    width: 100%;
      tr{
        border-top: 1px solid black;
        &:nth-of-type(6){
          border-bottom: 1px solid black;
        }
        th{
          @include fz(16);
          padding: 20px 0;
          width: 30%;
          text-align: center;
        }
        td{
          @include fz(16);
          padding: 20px 0;
          width: 70%;
        }
      }
    }
   
  }