html {
  font-size: 62.5%;
}

.pagecompany-info {
  width: 100%;
}

.pagecompany-info tr {
  border-top: 1px solid black;
}

.pagecompany-info tr:nth-of-type(6) {
  border-bottom: 1px solid black;
}

.pagecompany-info tr th {
  font-size: 1.6rem;
  padding: 20px 0;
  width: 30%;
  text-align: center;
}

.pagecompany-info tr td {
  font-size: 1.6rem;
  padding: 20px 0;
  width: 70%;
}

.pagevisual {
  height: 250px;
  width: 100%;
  background: url(../../../img/mv.png) center/cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagevisual-ttl {
  color: white;
}
